* {
  margin: 0;
  padding: 0;
}

.container {
  background: linear-gradient(to bottom, #0a447e, white);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app-wrapper {
  background: linear-gradient(to bottom, black, #2b2b2b);
  min-width: 600px;
  min-height: 650px;
  padding: 20px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-top: 40px;
  margin-bottom: 40px;
}

.header h1 {
  background: -webkit-linear-gradient(crimson, white);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  text-align: center;

}

.header p {
  text-align: center;
  color: white;
  margin-bottom: 30px;
}

.task-input {
  outline: none;
  width: 400px;
  padding: 15px;
  margin: 20px 10px;
  font-size: 20px;
  color: black;
  background-color: white;
  border: 3px solid white;
  border-radius: 10px;
}

.task-input:hover {
  border: 3px solid crimson;
}

.button-add {
  width: 80px;
  padding: 15px;
  font-size: 20px;
  border-radius: 10px;
  margin: 0 10px;
  border: 0;
  background: crimson;
  border: 2px solid crimson;
  cursor: pointer;
}

.button-add:hover {
  color: crimson;
  background: none;
}

.list-item {
  display: flex;
  margin: 20px 10px;
  border: 2px solid crimson;
  border-radius: 10px;
  padding: 10px;
  height: 40px;
}

.list-item:hover {
  border: 2px solid white;
}

.button-complete,
.button-delete,
.button-edit {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-complete {
  color: #38af48;
  margin-right: 10px;
}

.button-complete:hover {
  color: #ffffff;
  margin-right: 10px;
}

.button-edit {
  color: #ed7d0e;
  margin-right: 10px;
}

.button-edit:hover {
  color: #ffffff;
  margin-right: 10px;
}

.button-delete {
  color: #ed0e0e;
}

.button-delete:hover {
  color: #ffffff;
}

.complete {
  font-style: italic;
  text-decoration-style: wavy;
  text-decoration-line: line-through;
  text-decoration-color: white;
  opacity: 0.2;
}

.list {
  width: 380px;
  background-color: transparent;
  color: #fff;
  border: none;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

.list:hover {
  color: crimson;
}

input:focus,
button:focus {
  outline: none;
}

footer {
  background: transparent;
  padding: 20px 23px;
  text-align: center;
  color: black;
}

.ftr-div {
  display: flex;
  justify-content: center;
}